.message {
    display: flex;
    align-items: flex-end;
    justify-content: center;
    padding: 5px;
    margin-left: 150px;
}

.avatar {
    width: 50px;
    height: 50px;
    border-radius: 50%;
}

.content {
    padding: 10px;
    background-color: #282c34;
    border-radius: 7px 7px 0 7px;
    opacity: 0.8;
}

.angle {
    width: 20px;
    height: 20px;
    background: radial-gradient(circle at 20px 0, rgba(0, 0, 0, 0) 20px, #282c34 0);
    opacity: 0.8;
}

.name {
    color: blueviolet;
    font-weight: bold;
}

.text {
    color: white;
}

.time {
    display: flex;
    justify-content: flex-end;
    color: green;
}
